.navbar{
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    height: 90px;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    

    &__container{
        display:flex;
        align-items: center;
        width:100%;
        height: inherit;
        padding: 0px 20px;
        flex:1;

        &__logo{
            color:var(--white-theme-main-color);
            cursor:pointer;
        }

        &__menu{
            display:flex;
            list-style: none;
            text-align: center;

            &__item{
                line-height: 40px;
                margin-right: 1.5rem;
                font-size: 1.6rem;

                &::after{
                    content:'';
                    display: block;
                    height: 3px;
                    width: 0;
                    background: transparent;
                    transition: all 0.5s ease;
                }

                &:hover::after{
                    width: 100%;
                    background: var(--white-theme-main-color);
                }

                &__links{
                    color:var(--white-theme-main-color);
                    text-decoration: none;
                    padding:0.5rem;
                }
            }
        }
    }
}

.nav-icon{
    display:none;
}

@media only screen and (max-width: 768px){
    .navbar__container__menu{
        display:flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        position: absolute;
        top:80px;
        left:-110%;
        opacity: 1;
        transition: all 0.5s ease;

        &.active{
            background: var(--blue-theme-nav-background-color);
            left: 0px;
            opacity: 1;
        }
        
        &__item{
            border-bottom: 1px solid #98C9A3;
            margin:0px;
            padding: 20px 0px;
        }
    }

    .nav-icon{
        display: block;
        cursor: pointer;
        color: var(--white-theme-main-color);
        padding-right: 20px;
    }
}